import React from "react";
import styles from "./FindMemberForm.scss";
import classNames from "classnames/bind";

const cx = classNames.bind(styles);

const FindMemberForm = ({
  userId,
  username,
  userEmail1,
  userEmail2,
  onChangeInput,
  onFindUserId,
  onFindPassword
}) => {
  const handleChange = e => {
    const { name, value } = e.target;
    onChangeInput({ name, value });
  };

  return (
    <div className={cx("Form")}>
      <div className={cx("FindId")}>
        <div className={cx("TitleLabel")}> </div>
        <div className={cx("TitleBorder")} />
        <div className={cx("FindForm")}>
          <div className={cx("Input")}>
            <input
              type="text"
              name="username"
              placeholder=""
              value={username}
              onChange={handleChange}
            />
            <input
              type="text"
              name="userEmail1"
              placeholder="  "
              value={userEmail1}
              onChange={handleChange}
            />
          </div>
          <div className={cx("Button")} onClick={onFindUserId}>
             
          </div>
        </div>
      </div>
      <div className={cx("FindPassword")}>
        <div className={cx("TitleLabel")}> </div>
        <div className={cx("TitleBorder")} />
        <div className={cx("FindForm")}>
          <div className={cx("Input")}>
            <input 
              type="text" 
              name="userId" 
              placeholder=""
              value={userId}
              onChange={handleChange} />
            <input 
              type="text" 
              name="userEmail2" 
              placeholder="  "
              value={userEmail2}
              onChange={handleChange} />
          </div>
          <div className={cx("Button")} onClick={onFindPassword}> </div>
        </div>
      </div>
    </div>
  );
};

export default FindMemberForm;



